
import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { specialSymbol } from "app/@uisftech/common/service/validator";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";

@Component({
    selector: 'custom-mission-detail',
    templateUrl: './custom-mission-detail.component.html',
    styleUrls: ['./custom-mission-detail.component.scss'],
})
export class CustomMissionDetail implements OnInit {
    delete2: boolean = false;
    outStock1: boolean;
    lookkey: boolean;
    @Output() outValue = new EventEmitter();
    @Input() inValue: any;
    @Input() modifyValue: any;
    loadingShow: any = { flag: false }; // loading
    userform: FormGroup;
    msgs: Message[] = [];
    code: any;
    dataList: any = [];
    isMybankCustList: any;//
    customerTypeList: any;//
    statusIdList: any;//
    inListTypeList: any;//
    operationTypeList: any;//
    idTypeList: any;//
    inStock: boolean = false;//
    outStock: boolean = false;//
    nowProcessId: string;
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;// 
    bank: boolean = false;//
    bankown: boolean = false;//
    adjust: boolean = false;//
    efficacy: boolean = false;//
    delete: boolean = false;//
    diskey: boolean = true;
    constructor(
        public httpService: CusListManageHttpService,
        private messageService: MessageService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.userform = fb.group({
            operationType: [''],//
            isMybankCust: [''],//
            partyId: [''],//
            custName: [''],//
            listTypeOneName: [''],//
            listTypeTwoName: [''],//
            listTypeName: [''],//
            statusId: [''],//
            classifyId: [''],//
            inListType: [''],//
            toListReason: [''],//
            sourceDes: [''],//
            registerUserName: [''],//
            registerOrgName: [''],//
            registerTime: [''],//
            filePath: [''],//
            fileName: [''],//
            idType: [''],//
            idNo: [''],//
            cellphone: [''],//
            contactAddress: [''],//
            relatedListTypeIdName: [''],
            relatedlistTypeId: [''],
            releaseReason: ['']
        });
    }
    ngOnInit() {
        this.codeValues() //
        this.isMybankCustList = this.code['isMybankCust'];
        this.customerTypeList = this.code['customerType'];
        this.statusIdList = this.code['statusId'];
        this.inListTypeList = this.code['inListType'];
        this.operationTypeList = this.code['operation'];
        this.idTypeList = this.code['CERT_TYPE'];
        debugger
        console.log(this.modifyValue.listTaskId, "this.modifyValue.listTaskId")
        if (this.modifyValue == '1') {//
            this.bank = true;
            this.inStock = true;
            this.outStock = false;
            this.isMybank();
            debugger
        } else if (this.modifyValue == '2') {//
            this.bank = false;
            this.inStock = false;
            this.outStock = true;
            this.userform.get('sourceDes').setValue(this.inValue.sourceDes);
        } else if (this.modifyValue.listTaskId) {
            this.inStock = true;
            this.outStock = false;
            this.querySed();
        };

        if (this.inValue) {
            this.userform.get('operationType').setValue(this.inValue.operationType);
            this.userform.get('isMybankCust').setValue(this.inValue.isMybankCust);
            this.userform.get('partyId').setValue(this.inValue.partyId);
            this.userform.get('custName').setValue(this.inValue.custName);
            this.userform.get('listTypeOneName').setValue(this.inValue.listTypeOneName);
            this.userform.get('listTypeTwoName').setValue(this.inValue.listTypeTwoName);
            this.userform.get('listTypeName').setValue(this.inValue.listTypeName);
            this.userform.get('statusId').setValue(this.inValue.statusId);
            this.userform.get('classifyId').setValue(this.inValue.classifyId);
            this.userform.get('inListType').setValue(this.inValue.inListType);
            this.userform.get('toListReason').setValue(this.inValue.toListReason);
            this.userform.get('registerUserName').setValue(this.inValue.registerUserName);
            this.userform.get('registerOrgName').setValue(this.inValue.registerOrgName);
            this.userform.get('registerTime').setValue(this.inValue.registerTime);
            console.log(this.inValue.operationType);
            if (this.inValue.operationType == '1005') {//
                this.adjust = true;
                this.efficacy = false;
                this.delete = false;
                this.userform.get('relatedListTypeIdName').setValue(this.inValue.relatedListTypeIdName);
                this.userform.get('releaseReason').setValue(this.inValue.releaseReason);
            } else if (this.inValue.operationType == '1006') {//
                this.adjust = false;
                this.efficacy = true;
                this.delete = false;
                this.diskey = true;
                this.userform.get('releaseReason').setValue(this.inValue.releaseReason);
            } else if (this.inValue.operationType == '1003') {//
                this.adjust = false;
                this.efficacy = false;
                this.delete = true;
                this.diskey = true;
                this.userform.get('releaseReason').setValue(this.inValue.releaseReason);
            } else if (this.inValue.operationType == '1004') {//
                this.outStock1 = false;
                this.delete = true;
                this.delete2 = true;
                // alert("")
                this.userform.get('releaseReason').setValue(this.inValue.releaseReason);
            } else if (this.inValue.operationType == '1002') {//
                this.outStock1 = true;
                this.delete = false;

                // alert("")
                this.userform.get('releaseReason').setValue(this.inValue.releaseReason);
            }
        }
        debugger
        console.log(this.inValue, "this.inValue", this.modifyValue.taskFlowId, "this.modifyValue")
        if (this.inValue.taskFlowId || this.modifyValue.taskFlowId) {
            this.nowProcessId = this.inValue.taskFlowId || this.modifyValue.taskFlowId;
            this.lookkey = true
        } else if (this.modifyValue.listTaskId) {
            this.nowProcessId = this.modifyValue.listTaskId;
            this.lookkey = true
        } else {
            this.lookkey = false
        }

    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    isMybank() {
        if (this.inValue.isMybankCust == 'Y' || this.inValue.isMybankCust == '') {
            this.bank = true;
            this.bankown = false;
        } else if (this.inValue.isMybankCust == 'N') {
            this.bank = false;
            this.bankown = true;
            this.userform.get('idType').setValue(this.inValue.idType);
            this.userform.get('idNo').setValue(this.inValue.idNo);
            this.userform.get('cellphone').setValue(this.inValue.cellphone);
            this.userform.get('contactAddress').setValue(this.inValue.contactAddress);
        }
    }
    //-
    querySed() {
        const param = { listTaskId: this.modifyValue.listTaskId };
        this.httpService.selectListTaskInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.returnList ? data.returnList : [];
                this.userform.get('operationType').setValue(this.dataList[0].operationType);
                this.userform.get('isMybankCust').setValue(this.dataList[0].isMybankCust);
                this.userform.get('partyId').setValue(this.dataList[0].partyId);
                this.userform.get('custName').setValue(this.dataList[0].custName);
                this.userform.get('listTypeOneName').setValue(this.dataList[0].listTypeOneName);
                this.userform.get('listTypeTwoName').setValue(this.dataList[0].listTypeTwoName);
                this.userform.get('listTypeName').setValue(this.dataList[0].listTypeName);
                this.userform.get('statusId').setValue(this.dataList[0].statusId);
                this.userform.get('classifyId').setValue(this.dataList[0].classifyId);
                this.userform.get('inListType').setValue(this.dataList[0].inListType);
                this.userform.get('toListReason').setValue(this.dataList[0].toListReason);
                this.userform.get('registerUserName').setValue(this.dataList[0].registerUserName);
                this.userform.get('registerOrgName').setValue(this.dataList[0].registerOrgName);
                this.userform.get('registerTime').setValue(this.dataList[0].registerTime);

                if (this.dataList[0].operationType == '1005') {//
                    this.adjust = true;
                    this.efficacy = false;
                    this.delete = false;
                    this.userform.get('relatedListTypeIdName').setValue(this.dataList[0].relatedListTypeIdName);
                    this.userform.get('releaseReason').setValue(this.dataList[0].releaseReason);
                }
                else if (this.dataList[0].operationType == '1006') {//
                    this.adjust = false;
                    this.efficacy = true;
                    this.delete = false;
                    this.diskey = true;
                    this.userform.get('releaseReason').setValue(this.dataList[0].releaseReason);
                } else if (this.dataList[0].operationType == '1003') {//
                    this.adjust = false;
                    this.efficacy = false;
                    this.delete = true;
                    this.diskey = true;
                    this.userform.get('releaseReason').setValue(this.dataList[0].releaseReason);
                    this.outStock = false;
                } else if (this.inValue.operationType == '1004') {//
                    this.outStock1 = false;
                    this.delete = true;
                    this.userform.get('releaseReason').setValue(this.dataList[0].releaseReason);
                } else if (this.inValue.operationType == '1002') {//
                    this.outStock1 = true;
                    this.delete = false;
                    this.userform.get('releaseReason').setValue(this.dataList[0].releaseReason);
                }

            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
    //
    download() {
        // let obj = this.inValue.filePath.lastIndexOf('/');
        // let fileName = this.inValue.filePath.substring(0, obj) + '/';
        // let filePath = this.inValue.filePath.substring(obj + 1);
        // window.open(this.DOWNLOAD + "?fileName=" + encodeURI(fileName) + "&filePath=" + filePath, "_self")
        let arr = this.inValue.filePath.split('/');
        let path = '';
        for (let j = 1; j < arr.length - 1; j++) {
            path += '/' + arr[j];
        }
        path = path + '/';
        let donwname = arr[arr.length - 1];
        open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(donwname) + "&fileUrl=" + path, '_blank');
    }
    // 
    doReset() {
        this.outValue.emit(false);
    }
}