import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { specialSymbol, checkNumber, checkEnAndNum } from "app/@uisftech/common/service/validator";
import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { ConfirmationService, Message } from "primeng/primeng";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { debug } from 'util';
import { Dialog } from "primeng/components/dialog/dialog";
import { ViewChild } from "@angular/core";
@Component({
    selector: 'custom-mission-list',
    templateUrl: './custom-mission-list.component.html',
    styleUrls: ['./custom-mission-list.component.scss'],
    providers: []
})
export class CustomMissionListComponent implements OnInit {
    @ViewChild('dialog') dialog: Dialog;
    msgs: Message[] = [];
    userform: FormGroup;
    message: any = { msg: [] };
    loadingShow: any = { flag: false }; // loading
    tabMesg: any = tableMessage;//
    code: any;
    first: number = 0;//
    pageNum: number = 1;//
    pageSize: number = 10;//
    totalNum: number;//
    dataList: any = [];
    operationTypeList: any;//
    operationTypeListD: any;//
    operationTypeListP: any;//
    listTypeOneList: any;//
    approvalStatusList: any;//
    display: boolean = false;//
    showModel: any;//
    single: boolean = false;//
    batch: boolean = false;//
    addUpHeader: any;//
    inValue: any = [];//
    ch: any = CALENDAR_CN;//
    modifyValue: any;//
    orgTreeDisplay: boolean = false;//
    userTreeDisplay: boolean = false;//
    userInValue: any;
    jbxx: any = [];//
    listTask: any;
    dealPerShow: boolean = false;
    waitSelUserList: any;
    outVal: any;
    user: any;//
    operationType: any;//
    userName: any;//
    orgName: any;//
    cusDisplay: boolean = false; //
    constructor(
        public confirmationService: ConfirmationService,
        public httpService: CusListManageHttpService,
        private messageService: MessageService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.userform = fb.group({
            operaType: ['1'],//
            operationType: ['', Validators.required],//
            partyId: ['', [Validators.maxLength(20), checkNumber]],//
            custName: ['', [Validators.maxLength(100), specialSymbol]],//
            listTypeOne: [''],//
            idNo: ['', [Validators.maxLength(32), checkEnAndNum]],//
            registerUser: [''],//
            registerTimeStart: [''],//
            registerTimeEnd: [''],//
            registerOrg: [''],//
            approvalStatus: [''],//
            upListTaskId: [''],//
            registerOrgName: [''],//
            registerUserName: [''],//
        })
    }
    ngOnInit() {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));

        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.userName = chName.userName
        this.orgName = chName.orgName
        this.codeValues() //
        this.operationTypeList = this.code['operation'];
        this.operationTypeListD = this.code['operationTypeD'];
        this.operationTypeListP = this.code['operationTypeP'];
        this.approvalStatusList = this.code['approvalStatus'];
        this.singlebatch();
        setTimeout(() => {
            this.dataList = [];
        })
        this.queryOrgName()
    }
    queryOrgName() {
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        for (var i = 0; i < chName.orgList.length; i++) {
            if (this.user.extParam.orgId == chName.orgList[i].orgId) {
                this.orgName = chName.orgList[i].orgName
            }
        }
        console.log(this.orgName, "111111111")
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    queryType() {
        const param = {};
        param['listTypeHierarchy'] = 1;
        this.httpService.selectBlackListType(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

                let list = data.returnList;
                this.jbxx = [];
                list.forEach(element => {
                    let obj = {};
                    obj['label'] = element.listTypeName;
                    obj['value'] = element.listTypeId;
                    this.jbxx.push(obj);
                });
                this.jbxx.unshift({ label: '', value: '' });
                this.listTypeOneList = this.jbxx;

            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
    ///
    singlebatch() {
        if (this.userform.value.operaType == '1') {
            this.single = true;
            this.batch = false;
            setTimeout(() => {
                this.dataList = [];
            })
            this.clear();
            this.userform.get('operationType').setValue('');
        } else if (this.userform.value.operaType == '2') {
            this.single = false;
            this.batch = true;
            setTimeout(() => {
                this.dataList = [];
            })
            this.clear();
            this.queryType();
        }
    }
    // /
    openTree(param) {
        if (param == 'org') {
            this.orgTreeDisplay = !this.orgTreeDisplay;
        } else {
            this.userTreeDisplay = !this.userTreeDisplay;
        }
    }
    // -
    contacttreeCall(param) {
        this.orgTreeDisplay = false;
        this.userform.addControl('registerOrgName', this.fb.control(''));
        this.userform.get('registerOrg').setValue(param.orgId);
        this.userform.get('registerOrgName').setValue(param.orgName);
    }
    // -
    workerCall(item) {
        this.userTreeDisplay = false;
        if (item.length > 0) {
            this.userform.addControl('registerUserName', this.fb.control(''));
            this.userform.get('registerUser').setValue(item[0].tellerId);
            this.userform.get('registerUserName').setValue(item[0].tellerName);
        }
    }
    // 
    showTime(param1, param2) {
        if (param1 == 'plan') {
            const registerTimeStart = this.commfunc.transDateN(this.userform.value.registerTimeStart);
            const registerTimeEnd = this.commfunc.transDateN(this.userform.value.registerTimeEnd);
            if (registerTimeStart && registerTimeEnd) {
                if (registerTimeStart > registerTimeEnd) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.userform.value.registerTimeStart = null;
                        } else {
                            this.userform.value.planContactTimeTo = null;
                        }
                    })
                }
            }
        }
    }
    // 
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }
    //
    // partydetail() {
    //     const param = {};
    //     param['custNo'] = this.userform.value.partyId;
    //     if (this.batch) {
    //         this.cusDisplay = true;
    //     }
    //     this.httpService.selectCustBriefInfo(param).subscribe(data => {
    //         if (data.returnCode.code == 'success') {
    //             this.userform.get('custName').setValue(data.custName);
    //         } else {
    //             this.msgs = [];
    //             this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
    //         }
    //     }, err => {
    //         this.msgs = [];
    //         this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
    //     });
    // }
    //
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        if (this.userform.value.partyId || this.userform.value.custName || this.userform.value.idNo) {
            if (this.batch) {
                this.cusDisplay = true;
            }
        }
        this.queryClick();
    }
    //
    queryClick() {
        // alert(1111)
        this.userform.removeControl('registerUserName');
        this.userform.removeControl('registerOrgName');
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        param['registerTimeStart'] = this.commfunc.transDateN(this.userform.value.registerTimeStart);
        param['registerTimeEnd'] = this.commfunc.transDateN(this.userform.value.registerTimeEnd);
        this.httpService.selectListTaskInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

                this.dataList = data.returnList ? data.returnList : [];
                this.totalNum = data.total;

            } else {
                this.msgs = [];
                this.dataList = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
    // 
    update(col, i) {
        this.inValue = col;
        this.modifyValue = i;
        this.display = true;
        this.showModel = 1;
        if (this.userform.value.operaType == '1') {
            if (this.userform.value.operationType == '1001') {
                this.addUpHeader = '';
            } else if (this.userform.value.operationType == '1003') {
                this.addUpHeader = '';
            } else if (this.userform.value.operationType == '1005') {
                this.addUpHeader = '';
            } else if (this.userform.value.operationType == '1006') {
                this.addUpHeader = '';
            }
        } else if (this.userform.value.operaType == '2') {
            if (this.userform.value.operationType == '1002') {
                this.addUpHeader = '';
            } else if (this.userform.value.operationType == '1004') {
                this.addUpHeader = '';
            }
        }
    }
    // 
    updateCall(param) {
        this.display = param;
        this.pageNum = 1;
        // if (this.userform.value.operationType) {
        //     this.queryClick();
        // }
        this.queryClick();
    }
    // 
    detail(col, i) {
        this.modifyValue = i;
        this.inValue = col;
        console.log(this.inValue, "111111111111111111111111111")
        this.display = true;
        this.showModel = 2;
        if (this.userform.value.operaType == '1') {
            this.addUpHeader = '';
        } else if (this.userform.value.operaType == '2') {
            if (this.userform.value.operationType == '1002') {
                this.addUpHeader = '';
            } else if (this.userform.value.operationType == '1004') {
                this.addUpHeader = '';
            }
        }
    }
    // 
    detailCall(param) {
        this.display = param;
        this.pageNum = 1;
    }
    // 
    delete(item) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                let param = {
                    listTaskId: item.listTaskId,
                };
                this.httpService.deleteListTaskInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
                        // if (this.userform.value.operationType) {
                        this.queryClick();
                        // }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            },
            reject: () => {
            }
        });
    }
    keylistTypeTwo: any;
    keylistTypeOne: any;
    // 
    modify(item) {
        this.keylistTypeOne = item.listTypeOne;
        this.keylistTypeTwo = item.listTypeTwo;
        this.listTask = item.listTaskId;
        this.operationType = item.operationType;
        this.waitSelUserList = this.commfunc.query('KHYY_MDJSP', {});
        if (!this.waitSelUserList.erro) {
            this.outVal = this.waitSelUserList.waitSelUserList;
            let perPageShow = this.waitSelUserList.display;
            if (perPageShow == true) {
                this.dealPerShow = true;
            } else {
                const param = { listTaskId: item.listTaskId, operationType: item.operationType };
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                param['lastUpdatedChannel'] = '2';
                console.log(param, "1111111111111111111")

                this.httpService.updateListTaskInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.dealPerShow = false;
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
        }
    }
    outDisplayCall(event) {
        setTimeout(() => {
            this.dealPerShow = event;
        }, 4000)
    }
    outValueCall(event) {
        console.log(event, "")
        this.outVal = event;
        let ss = '';
        let specifyNextNode = '';
        let outFlowId;
        this.outVal.forEach(item => {
            ss += item.tellerId;
            specifyNextNode = item.orgBank;
            outFlowId = item.outFlowId;
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        commonHeader.extParam['specifyNextNode'] = outFlowId;

        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        const param = { listTaskId: this.listTask, operationType: this.operationType };
        param['lastAuthUser'] = ss;
        param['lastAuthOrg'] = specifyNextNode;
        param['lastUpdateOrg'] = this.user.extParam.orgId;
        param['lastUpdateUser'] = this.user.userId;
        param['approvalStatus'] = 'AB';
        param['listTypeOne'] = this.keylistTypeOne;
        param['listTypeTwo'] = this.keylistTypeTwo;
        param['registerUserName'] = this.userName
        param['registerOrgName'] = this.orgName
        param['registerOrg'] = this.user.extParam.orgId;
        param['registerUser'] = this.user.userId;
        this.httpService.updateListTaskInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // this.dealPerShow = false;

                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
                setTimeout(() => {
                    this.dealPerShow = false;
                }, 1000)
                this.queryClick();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })

        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers || s.extParam.specifyNextNode) {
            delete s.extParam.specifyNextOpers;
            delete s.extParam.specifyNextNode;
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }
    // 
    reset() {
        this.userform.get('operaType').setValue(1);
        this.clear();
        this.singlebatch();
        this.cusDisplay = false;
    }
    // 
    clear() {
        this.userform.addControl('registerUserName', this.fb.control(''));
        this.userform.addControl('registerOrgName', this.fb.control(''));
        this.userform.get('operationType').setValue('');
        this.userform.get('partyId').setValue('');
        this.userform.get('listTypeOne').setValue('');
        this.userform.get('idNo').setValue('');
        this.userform.get('custName').setValue('');
        this.userform.get('registerUser').setValue('');
        this.userform.get('registerTimeStart').setValue('');
        this.userform.get('registerTimeEnd').setValue('');
        this.userform.get('registerOrg').setValue('');
        this.userform.get('approvalStatus').setValue('');
        this.userform.get('registerUserName').setValue('');
        this.userform.get('registerOrgName').setValue('');
    }
    paginate(event) {
        //
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        // if (this.userform.value.operationType) {
        //     this.queryClick();
        // }
        this.queryClick();
    }
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    ngAfterViewInit() {
        this.cancelDialogShake();
    }
    cancelDialogShake () {
        if (this.dialog) {
            this.dialog.center = function () {
                let elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                let elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                if (elementWidth === 0 && elementHeight === 0) {
                  this.containerViewChild.nativeElement.style.visibility = 'hidden';
                  this.containerViewChild.nativeElement.style.display = 'block';
                  elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                  elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                  this.containerViewChild.nativeElement.style.display = 'none';
                  this.containerViewChild.nativeElement.style.visibility = 'visible';
                }
                let viewport = this.domHandler.getViewport();
                let x = Math.max(Math.ceil((viewport.width - elementWidth)) / 2, 0);
                let y = Math.max(Math.ceil((viewport.height - elementHeight)) / 2, 0);
                // 
                if (!this.containerViewChild.nativeElement.style.left ||
                  !this.containerViewChild.nativeElement.style.top ||
                  Math.abs(this.containerViewChild.nativeElement.style.left - x) > 5 ||
                  Math.abs(this.containerViewChild.nativeElement.style.top - y) > 5
                ) {
                  this.containerViewChild.nativeElement.style.left = x + 'px';
                  this.containerViewChild.nativeElement.style.top = y + 'px';
                }
              };
        }

    }
}