
import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { specialSymbol, checkCard, checkNumber } from "app/@uisftech/common/service/validator";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { TZB_CUS_DOWNLOAD, TZB_CUS_UPLOAD } from "app/@uisftech/common/service/http.constant";
import { Router, ActivatedRoute } from '@angular/router';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { BussinessMap } from 'app/pages/tzb/exam-appr-comp/custom/custom-group/bean/AppproResult.bean';
@Component({
    selector: 'custom-mission-update',
    templateUrl: './custom-mission-update.component.html',
    styleUrls: ['./custom-mission-update.component.scss'],
})
export class CustomMissionUpdate implements OnInit {
    disableclick: boolean;
    taskId: any;
    webtype: any;
    Header: any;
    @Output() outValue = new EventEmitter();
    @Input() inValue;
    @Input() modifyValue: any;
    loadingShow: any = { flag: false }; // loading
    userform: FormGroup;
    msgs: Message[] = [];
    code: any;
    isMybankCustList: any;//
    customerTypeList: any;//
    statusIdList: any;//
    inListTypeList: any;//
    listTypeOneList: any;//
    listTypeTwoList: any;//
    listTypeIdList: any;//
    idTypeList: any;//
    inStock: boolean = false;//
    outStock: boolean = false;//
    user: any;//
    // 
    files: any = [];
    fileList: any = [];
    // 
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    UPLOAD: string = TZB_CUS_UPLOAD;
    jbxx: any = [{ label: '', value: '' }];//
    reList: any = [];//
    bank: boolean = false;//
    bankown: boolean = false;//
    dealPerShow: boolean = false;
    waitSelUserList: any;
    outVal: any;
    adjust: boolean = false;//
    efficacy: boolean = false;//
    delete: boolean = false;//
    diskey: boolean = false;//
    diskey1: boolean = false;//
    kk: boolean = false;
    kk1: boolean = false;
    nowProcessId: any;
    lookkey: boolean = false
    detailList: any;
    userName: any;//
    orgName: any;//
    constructor(
        public customService: CustomeHttpService,
        public activatedRoute: ActivatedRoute,
        public httpService: CusListManageHttpService,
        private messageService: MessageService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        debugger
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.userName = chName.userName
        this.orgName = chName.orgName
        this.activatedRoute.queryParams.subscribe(queryParams => {
            if (JSON.stringify(queryParams) == '{}' && JSON.stringify(this.activatedRoute['snapshot']['params']) != '{}') {
                // if (this.inValue.taskFlowId || this.modifyValue.taskFlowId) {
                //     this.nowProcessId = this.inValue.taskFlowId || this.modifyValue.taskFlowId;
                //     this.lookkey = true
                // } else {
                //     this.lookkey = false
                // }
                this.taskId = this.activatedRoute['snapshot']['params'].taskId;
                let detprm = JSON.parse(this.activatedRoute['snapshot']['params']['dataMap'])
                // console.log(this.activatedRoute['snapshot']['params'], "")
                this.detailList = this.activatedRoute['snapshot']['params']
                this.webtype = '1';
                if (detprm['operationType'] == '1001') {//
                    this.modifyValue = '1';
                    this.Header = '';
                } else if (detprm['operationType'] == '1003') {//
                    this.modifyValue = '1';
                    this.Header = '';
                } else if (detprm['operationType'] == '1005') {//
                    this.modifyValue = '1';
                    this.Header = '';
                } else if (detprm['operationType'] == '1006') {//
                    this.modifyValue = '1';
                    this.Header = '';
                } else if (detprm['operationType'] == '1002') {//
                    this.modifyValue = '2';
                    this.Header = '';
                } else if (detprm['operationType'] == '1004') {//'
                    this.modifyValue = '2';
                    this.Header = '';
                }
                this.inValue = detprm
            } else {
            }
        })
        // MDJLCFQR
        this.userform = fb.group({
            isMybankCust: [''],//
            customerType: [''],//
            partyId: [''],//
            custName: [''],//
            listTypeOne: ['', Validators.required],//
            listTypeTwo: ['', Validators.required],//
            listTypeId: ['', Validators.required],//
            statusId: [''],//
            classifyId: [''],//
            inListType: [''],//
            sourceDes: [''],//
            idType: [''],//
            idNo: [''],//
            cellphone: [''],//
            contactAddress: ['', [Validators.maxLength(255), specialSymbol]],//
            // toListReason: ['', [Validators.maxLength(255), specialSymbol]],//
            toListReason: [''],//
            registerUser: [''],//
            registerOrg: [''],//
            registerTime: [''],//
            filePath: [''],//
            fileName: [''],//
            releaseReason: [''],//
            relatedListTypeIdName: [''],//
            relatedlistTypeId: [''],
            registerUserName: [''],//
            registerOrgName: [""],//
        });
    }
    ngOnInit() {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.codeValues() //
        this.isMybankCustList = this.code['isMybankCust'];
        this.customerTypeList = this.code['customerType'];
        this.statusIdList = this.code['statusId'];
        this.inListTypeList = this.code['inListType'];
        this.idTypeList = this.code['CERT_TYPE'];
        // console.log(this.inValue, "this.inValue")
        this.query();//
        let a = [this.inValue.listTypeOne, this.inValue.listTypeTwo, this.inValue.listTypeId]
        for (let e = 1; e < 3; e++) {
            let b = {
                value: a[e - 1]
            }
            setTimeout(() => {
                this.listType(e, b);
            }, 200);
        }
        if (this.modifyValue == '1') {//
            debugger
            if (this.inValue.operationType == '1005') {//
                this.adjust = true;
                this.efficacy = false;
                this.delete = false;
                this.diskey = true;
                this.userform.get('relatedlistTypeId').setValue(this.inValue.relatedlistTypeId);
            } else if (this.inValue.operationType == '1006') {//
                this.adjust = false;
                this.efficacy = true;
                this.delete = false;
                this.diskey = true;
            } else if (this.inValue.operationType == '1003') {//
                this.adjust = false;
                this.efficacy = false;
                this.delete = true;
                this.diskey = true;
                this.kk = true;
            } else if (this.inValue.operationType == '1001') { //
                this.userform.removeControl('partyId');
                this.userform.removeControl('customerType');
                this.userform.addControl('partyId', this.fb.control('', [Validators.required, Validators.maxLength(20), checkNumber]));
                if (this.userform.value.isMybankCust == 'N') {
                    this.userform.addControl('customerType', this.fb.control('', Validators.required));
                } else {
                    this.userform.addControl('customerType', this.fb.control(''));
                }
            }
            this.bank = true;
            this.inStock = true;
            this.outStock = false;
            this.userform.get('partyId').setValue(this.inValue.partyId);
            this.userform.get('isMybankCust').setValue(this.inValue.isMybankCust);
            this.userform.get('customerType').setValue(this.inValue.customerType);
            this.userform.get('custName').setValue(this.inValue.custName);
            this.userform.get('listTypeOne').setValue(this.inValue.listTypeOne);
            this.userform.get('listTypeTwo').setValue(this.inValue.listTypeTwo);
            this.userform.get('listTypeId').setValue(this.inValue.listTypeId);
            this.userform.get('statusId').setValue(this.inValue.statusId);
            this.userform.get('classifyId').setValue(this.inValue.classifyId);
            this.userform.get('toListReason').setValue(this.inValue.toListReason);
            this.userform.get('registerUserName').setValue(this.inValue.registerUserName);
            this.userform.get('registerOrgName').setValue(this.inValue.registerOrgName);
            this.userform.get('releaseReason').setValue(this.inValue.releaseReason);
            this.userform.get('idType').setValue(this.inValue.idType);
            this.userform.get('idNo').setValue(this.inValue.idNo);
            this.isMybank();
        } else if (this.modifyValue == '2') {//
            debugger
            this.bank = false;
            this.inStock = false;
            this.outStock = true;
            this.kk = true;
            this.kk1 = true;
            this.userform.removeControl('partyId');
            this.userform.removeControl('customerType');
            this.userform.removeControl('listTypeTwo');
            this.userform.removeControl('listTypeId');
            this.userform.addControl('listTypeTwo', this.fb.control(''));
            this.userform.addControl('listTypeId', this.fb.control(''));
            this.userform.get('sourceDes').setValue(this.inValue.sourceDes);
            this.userform.get('registerUserName').setValue(this.inValue.registerUserName);
            this.userform.get('registerOrgName').setValue(this.inValue.registerOrgName);
            this.userform.get('registerTime').setValue(this.inValue.registerTime);
            this.userform.get('idType').setValue(this.inValue.idType);
            this.userform.get('idNo').setValue(this.inValue.idNo);
            this.userform.get('filePath').setValue(this.inValue.filePath);
            if (this.inValue.operationType == '1004') {//
                this.diskey1 = true;
            }
        }
        if (this.inValue) {
            debugger
            this.userform.get('listTypeOne').setValue(this.inValue.listTypeOne);
            this.userform.get('inListType').setValue(this.inValue.inListType);
            this.userform.get('toListReason').setValue(this.inValue.toListReason);
            if (this.inValue.filePath) {
                let arr = this.inValue.filePath.split('/');
                let path = '';
                for (let j = 1; j < arr.length - 1; j++) {
                    path += '/' + arr[j];
                }
                path = path + '/';
                // this.userform.value.filePath = path;
                let donwname = arr[arr.length - 1];
                this.fileList = [{
                    fileName: arr[arr.length - 1]
                }];
            }
        }
        this.queryOrgName();

    }
    queryOrgName() {
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        // console.log(this.user, "this.user")
        for (var i = 0; i < chName.orgList.length; i++) {
            if (this.user.extParam.orgId == chName.orgList[i].orgId) {
                this.orgName = chName.orgList[i].orgName
            }
        }
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    query() {
        this.jbxx = [{ label: '', value: '' }]
        const param = {};
        param['listTypeHierarchy'] = 1;

        this.httpService.selectBlackListType(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let list = data.returnList;
                list.forEach(element => {
                    let obj = {};
                    obj['label'] = element.listTypeName;
                    obj['value'] = element.listTypeId;
                    this.jbxx.push(obj);
                });

                this.listTypeOneList = this.jbxx;
                this.jbxx = null;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
    //
    jbxx1: any = [{ label: '', value: '' }];
    listType(i, t, j?) {
        debugger
        this.jbxx = [{ label: '', value: '' }]
        if (i == '1') {
            if (j == true) {
                this.listTypeTwoList = null;
            }
            const param = {};
            param['listTypeHierarchy'] = 2;
            param['parentListTypeId'] = t.value;
            this.httpService.selectBlackListType(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let list = data.returnList;
                    list.forEach(element => {
                        let obj = {};
                        obj['label'] = element.listTypeName;
                        obj['value'] = element.listTypeId;
                        this.jbxx.push(obj);
                    })
                    this.listTypeTwoList = this.jbxx;
                    this.jbxx = [{ label: '', value: '' }];
                    //this.listTypeIdList = [];  //
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: "" });
            })
        } else if (i == '2') {
            if (j == true) {
                this.listTypeIdList = null;
            }
            const param = {};
            param['listTypeHierarchy'] = 3;
            param['parentListTypeId'] = t.value;
            this.httpService.selectBlackListType(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.reList = data.returnList;
                    this.reList = data.returnList;
                    if (data.returnList.length == 0) {
                        this.userform.get('classifyId').setValue("");
                    }
                    let list = data.returnList;
                    list.forEach(element => {
                        let obj = {};
                        obj['label'] = element.listTypeName;
                        obj['value'] = element.listTypeId;
                        this.jbxx1.push(obj);
                    });

                    this.listTypeIdList = this.jbxx1;
                    this.jbxx1 = [{ label: '', value: '' }];
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: "" });
            })
        } else if (i == '3') {
            this.jbxx = [{ label: '', value: '' }]
            for (let e = 0; e < this.reList.length; e++) {
                if (j == true) {
                    if (this.userform.value.listTypeId == this.reList[e].listTypeId) {
                        this.userform.get('classifyId').setValue(this.reList[e].classifyId);
                    }
                }
            }
        }
    }
    //
    isMybank() {
        if (this.userform.value.isMybankCust == 'Y' || this.userform.value.isMybankCust == '') {
            this.bank = true;
            this.bankown = false;
            this.userform.removeControl('idType');
            this.userform.removeControl('idNo');
            this.userform.removeControl('cellphone');
        } else if (this.userform.value.isMybankCust == 'N') {
            this.bank = false;
            this.bankown = true;
            this.userform.removeControl('partyId');
            this.userform.addControl('idType', this.fb.control('', [Validators.required]));
            this.userform.addControl('cellphone', this.fb.control('', [Validators.maxLength(20), Validators.pattern(/^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/)]));
            this.userform.addControl('idNo', this.fb.control('', [Validators.required, Validators.maxLength(32), checkCard]));
            this.userform.get('idType').setValue(this.inValue.idType);
            this.userform.get('idNo').setValue(this.inValue.idNo);
            this.userform.get('cellphone').setValue(this.inValue.cellphone);
            this.userform.get('contactAddress').setValue(this.inValue.contactAddress);
        }
    }
    //
    partydetail() {
        const param = {};
        param['custNo'] = this.userform.value.partyId;
        this.httpService.selectCustBriefInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.userform.get('custName').setValue(data.custName);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    workword: number = 0;
    // -
    load(event) {
        // isUpdateFile
        if (event.xhr.response) {
            this.fileList = JSON.parse(event.xhr.response);
            if (this.fileList.success == false) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: this.fileList.errorMsg });
                return
            }
        }
        if (this.fileList[0]) {
            this.workword = 1;
            this.userform.get('filePath').setValue(this.fileList[0].fileUrl);
            this.userform.get('fileName').setValue(this.fileList[0].fileName);
        }
    }
    //
    updateListTaskManager() {

        let param = {
            inputParams: { listTaskId: this.inValue.listTaskId }
        }
        this.httpService.updateListTaskManager(param).subscribe(data => {

            if (data.returnCode.code == 'success') {
                // console.log(data, "111111111111111111")
                // console.log(param, "2222222")
                let message = data.returnCode.message
                this.doReset(message)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }




    // /
    doSave(i, funtype?) {
        if (this.modifyValue == '1') {
            if (this.userform.value.listTypeId == this.userform.value.relatedlistTypeId) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                return false
            }
            //
            if (i == '1') {//
                const param = Object.assign({}, this.userform.value);
                param['lastUpdateOrg'] = this.user.extParam.orgId;
                param['lastUpdateUser'] = this.user.userId;
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                param['listTaskId'] = this.inValue.listTaskId;
                // param['listTypeId'] = this.inValue.listTypeId;
                param['approvalStatus'] = 'AA';
                param['operationType'] = this.inValue.operationType;
                // console.log('', param)
                this.httpService.updateListTaskInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        let message = data.returnCode.message
                        if (this.webtype !== '1') {
                            this.doReset(message)
                        } else {
                            this.msgs = [];
                            this.msgs = [{ severity: 'success', summary: '', detail: message }];
                            setTimeout(() => {
                                window.history.go(-1)
                            }, 1000)
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }

            if (i == '2') {//
                this.waitSelUserList = this.commfunc.query('KHYY_MDJSP', {});
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let perPageShow = this.waitSelUserList.display;
                    if (perPageShow == true && !funtype) {
                        this.dealPerShow = true;
                    } else {
                        const param = Object.assign({}, this.userform.value);
                        param['lastUpdateOrg'] = this.user.extParam.orgId;
                        param['lastUpdateUser'] = this.user.userId;
                        param['listTaskId'] = this.inValue.listTaskId;
                        param['registerUserName'] = this.userName
                        param['registerOrgName'] = this.orgName
                        param['registerOrg'] = this.user.extParam.orgId;
                        param['registerUser'] = this.user.userId;
                        // param['listTypeId'] = this.inValue.listTypeId;
                        param['approvalStatus'] = 'AB';
                        param['operationType'] = this.inValue.operationType;
                        // console.log(funtype);
                        if (!funtype) {
                            param['lastUpdatedChannel'] = '1';
                        } else if (funtype) {
                            param['lastUpdatedChannel'] = '3';
                        }
                        this.httpService.updateListTaskInfo(param).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                let message = data.returnCode.message
                                this.doReset(message)
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: "" });
                        })
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
                }
            }
        } else if (this.modifyValue == '2') {//
            if (i == '1') {//
                const param = Object.assign({}, this.userform.value);
                param['lastUpdateOrg'] = this.user.extParam.orgId;
                param['lastUpdateUser'] = this.user.userId;
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                param['listTaskId'] = this.inValue.listTaskId;
                // param['listTypeId'] = this.inValue.listTypeId;
                param['approvalStatus'] = 'AA';
                param['operationType'] = this.inValue.operationType;
                if (this.workword == 1) {
                    param['isUpdateFile'] = 'Y';
                }
                this.httpService.updateListTaskInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        let message = data.returnCode.message
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '', detail: message }];
                        this.doReset(message)
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
            if (i == '2') {//
                this.waitSelUserList = this.commfunc.query('KHYY_MDJSP', {});
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let perPageShow = this.waitSelUserList.display;
                    if (perPageShow == true && !funtype) {
                        this.dealPerShow = true;
                    } else {
                        const param = Object.assign({}, this.userform.value);
                        param['lastUpdateOrg'] = this.user.extParam.orgId;
                        param['lastUpdateUser'] = this.user.userId;
                        param['listTaskId'] = this.inValue.listTaskId;
                        // param['listTypeId'] = this.inValue.listTypeId;
                        param['approvalStatus'] = 'AB';
                        param['operationType'] = this.inValue.operationType;
                        param['registerUserName'] = this.userName
                        param['registerOrgName'] = this.orgName
                        param['registerOrg'] = this.user.extParam.orgId;
                        param['registerUser'] = this.user.userId;
                        if (!funtype) {
                            param['lastUpdatedChannel'] = '1';
                        } else if (funtype) {
                            param['lastUpdatedChannel'] = '3';
                        }
                        if (this.workword == 1) {
                            param['isUpdateFile'] = 'Y';
                        }
                        this.httpService.updateListTaskInfo(param).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                let message = data.returnCode.message;
                                this.msgs = [];
                                this.msgs = [{ severity: 'success', summary: '', detail: message }];
                                this.doReset(message)
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: "" });
                        })
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
                }
            }
        }
    }
    outDisplayCall(event) {
        setTimeout(() => {
            this.dealPerShow = event;
        }, 4000)
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        let outFlowId;
        let specifyNextNode = '';
        this.outVal.forEach(item => {
            ss += item.tellerId;
            specifyNextNode = item.userBank;
            outFlowId = item.outFlowId;
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        commonHeader.extParam['specifyNextNode'] = outFlowId;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        if (this.modifyValue == '1') {
            const param = Object.assign({}, this.userform.value);
            param['lastAuthUser'] = ss;
            param['lastAuthOrg'] = specifyNextNode;
            param['lastUpdateOrg'] = this.user.extParam.orgId;
            param['lastUpdateUser'] = this.user.userId;
            param['listTaskId'] = this.inValue.listTaskId;
            // // param['listTypeId'] = this.inValue.listTypeId;
            param['approvalStatus'] = 'AB';
            param['operationType'] = this.inValue.operationType;
            param['blackListId'] = this.inValue.blackListId;
            param['isAdjust'] = this.inValue.isAdjust;
            param['taskId'] = this.taskId;
            param['userId'] = this.user.userId;
            if (this.webtype == '1') {
                let bussinessMap = {
                    specifyNextOpers: ss,
                    specifyNextNode: outFlowId,
                    dataMap: {}
                };
                Object.assign(bussinessMap['dataMap'], this.userform.value);
                param["approveMap"] = { result: "0" }
                param.bussinessMap = bussinessMap;
                param.bussinessMap.dataMap['lastAuthUser'] = ss;
                param.bussinessMap.dataMap['lastAuthOrg'] = specifyNextNode;
                param.bussinessMap.dataMap['lastUpdateOrg'] = this.user.extParam.orgId;
                param.bussinessMap.dataMap['lastUpdateUser'] = this.user.userId;
                param.bussinessMap.dataMap['listTaskId'] = this.inValue.listTaskId;
                param.bussinessMap.dataMap['approvalStatus'] = 'AB';
                param.bussinessMap.dataMap['blackListId'] = this.inValue.blackListId;
                param.bussinessMap.dataMap['operationType'] = this.inValue.operationType;
                param['isAdjust'] = this.inValue.isAdjust;
                this.customService.bpmNextTask(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        let message = data.returnCode.message
                        this.doSave(2, 'backtype')
                        this.updateListTaskManager()
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '', detail: message }];
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            } else {
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                this.httpService.updateListTaskInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        let message = data.returnCode.message
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '', detail: message }];
                        this.doReset(message)
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        } else if (this.modifyValue == '2') {
            const param = Object.assign({}, this.userform.value);
            param['lastAuthUser'] = ss;
            param['lastAuthOrg'] = specifyNextNode;
            param['lastUpdateOrg'] = this.user.extParam.orgId;
            param['lastUpdateUser'] = this.user.userId;
            param['listTaskId'] = this.inValue.listTaskId;
            // param['listTypeId'] = this.inValue.listTypeId;
            param['approvalStatus'] = 'AB';
            param['blackListId'] = this.inValue.blackListId;
            param['operationType'] = this.inValue.operationType;
            param['isAdjust'] = this.inValue.isAdjust;
            param['taskId'] = this.taskId;
            param['userId'] = this.user.userId;
            param["approveMap"] = { result: "0" }
            if (this.workword == 1) {
                param['isUpdateFile'] = 'Y';
            }
            if (this.webtype == '1') {
                let bussinessMap = {
                    specifyNextOpers: ss,
                    specifyNextNode: outFlowId,
                    dataMap: {}
                };
                Object.assign(bussinessMap['dataMap'], this.userform.value);
                param.bussinessMap = bussinessMap;
                param.bussinessMap.dataMap['lastAuthUser'] = ss;
                param.bussinessMap.dataMap['lastAuthOrg'] = specifyNextNode;
                param.bussinessMap.dataMap['lastUpdateOrg'] = this.user.extParam.orgId;
                param.bussinessMap.dataMap['lastUpdateUser'] = this.user.userId;
                param.bussinessMap.dataMap['listTaskId'] = this.inValue.listTaskId;
                param.bussinessMap.dataMap['approvalStatus'] = 'AB';
                param.bussinessMap.dataMap['blackListId'] = this.inValue.blackListId;
                param.bussinessMap.dataMap['operationType'] = this.inValue.operationType;
                param['isAdjust'] = this.inValue.isAdjust;
                this.customService.bpmNextTask(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        let message = data.returnCode.message
                        this.doSave(2, 'backtype')
                        this.updateListTaskManager()
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '', detail: message }];
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            } else {
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                this.httpService.updateListTaskInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        let message = data.returnCode.message
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '', detail: message }];
                        this.doReset(message)
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }

        }
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers || s.extParam.specifyNextNode) {
            delete s.extParam.specifyNextOpers;
            delete s.extParam.specifyNextNode;
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }
    // 
    doReset(message?) {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: '!' });
        if (this.webtype == '1') {
            window.history.go(-1)
        } else {
            setTimeout(() => {
                this.outValue.emit(false)
            }, 300)
        }
    }
}